import style from './loader.module.css';
import { LoaderSvg } from './loader.svg';
import {FC} from "react";

type TSizes = 'small' | 'medium' | 'large'

const loaderSizes: {
  [key in TSizes]: number
} = {
  small: 16,
  medium: 24,
  large: 40
};

export const Loader: FC<{size: TSizes, inverse?: boolean}> = ({ size, inverse = false }) => {
  const loaderColor = inverse ? '#fff' : '#3C39EC';

  const wrapperStyleKey = 'wrapper_' + size;
  return (
    <div className={style[wrapperStyleKey]}>
      <LoaderSvg color={loaderColor} size={loaderSizes[size]} />
    </div>
  );
};
